@import url(https://fonts.googleapis.com/css?family=Montserrat:300,700&display=swap);
body {
	margin: 50px;
	font-family: 'Montserrat', sans-serif;
}

button,
input {
	margin-left: 10px;
	padding: 3px 2%;
	cursor: pointer;
	outline: none;
	border: 1px solid;
	background: white;
}

button:hover,
li input:hover {
	background: linear-gradient(rgba(0, 255, 255, 0.5), rgba(0, 0, 255, 0.5));
}

input:focus {
	outline: 1px solid rgba(0, 255, 255, 0.5);
	box-shadow: 0 2px 3px 0 rgba(0, 0, 255, 0.5);
}
